.button {
    padding: 10px 15px;
    font-family: inherit;
    background: #efc12f;
    border: 2px solid transparent;
    color: white;
    font-size: 18px;
    border-radius: 5px;
    cursor: pointer;
    transition: 300ms;

    &:disabled {
        background: grey;
        opacity: 80%;
        pointer-events: none;
    }

    &.big {
        font-size: 15px;
        transition: 300ms;
        font-weight: 600;
    }
    &.red:hover  {
      color: crimson;
    }

    & span + i {
        margin-left: 10px;
    }

    &:hover {
        background: white;
        border-color: #efc12f;
        color: #efc12f;
    }

    &.circle {
        border-radius: 50%;
        background: rgba(128, 128, 128, 0.65);
        font-size: 20px;
        aspect-ratio: 1/1;
        padding: 10px;

        &:hover {
            background: white;
            /*color: #efc12f;*/
        }
    }

    &.table-remove {
        font-size: 15px;
        padding: 0;
        background: transparent;
        color: crimson;
    }
}

.button-group {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;

    & button {
        background: white;
        color: #efc12f;
        border-color: #efc12f;
    }

    & button.active {
       /*background: #2bbef9;*/
        background: #efc12f;
        color: white;
    }
}
.button.on {

}
.button.off {
    background: lightgrey;
}
