.product-card-container {
    background: lightgrey;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    & h1 {
        font-size: 30px;
        color: #222222;
    }
    & img {
        aspect-ratio: 1/1;
        width: 350px;
    }
}

.product-card {
    display: flex;
    align-items: flex-start;
    gap: 20px;
}

.product-card-info {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: flex-start;
    justify-content: space-between;
    height: 100%;

    & button {
        align-self: flex-end;
        margin-left: 20px;

        &.red {
            background: crimson;

            &:hover {
                border: 2px solid crimson;
                background: white;
            }
        }
    }
}

.product-card-wrapper {
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: white;
    padding: 35px;
    border-radius: 10px;
    position: relative;

    & a {
        padding: 10px 15px;
        background: #efc12f;
        color: #222222;
        font-weight: bold;
        clip-path: polygon(40% 0%, 40% 20%, 100% 20%, 100% 80%, 40% 80%, 40% 100%, 0% 50%);
        align-self: flex-start;
        font-family: "Leckerli One", sans-serif;
        position: absolute;
        top: 1px;
        left: 35px;
    }
}